//菜单处理工具的最重要的目的就是把后端传过来的component字符串转换成为对象，因为
//传递给routes 里面的component就是一个页面的的对象。
import {getRequest} from "./api";

export const initMenu = (router, store) => {
    if (store.state.routes.length > 0) {
        //console.log(11);
        return;  //如果是这个那么说明菜单已经被加载
    }
    getRequest("/system/config/menu").then(data => { //请求服务端 data就是返回的数据
        if (data) {
            console.log(data);
            let fmtRoutes = formatRoutes(data);
            router.addRoutes(fmtRoutes);
            store.commit('initRoutes',fmtRoutes);  //store中的initRoutes
        }
    })
};

export const formatRoutes = (routes) => {  //格式化后端返回的数据
    let fmRoutes = [];
    routes.forEach(router => {
        let {
            path,
            component,
            name,
            iconcls,
            meta,
            children
        } = router;
        if (children && children instanceof Array) { //如果是这种则表明是父类菜单
            children = formatRoutes(children);  //回调
        }
        let fmRouter = {
            path:path,
            name:name,
            iconcls:iconcls,
            meta:meta,
            children:children,
            component(resolve) {
                if (component.startsWith("Home")) {
                    require(['../views/Back.vue'], resolve)  //这个拼接就等于把引入路径拼接出来
                }else if (component.startsWith("Emp")){
                    require(['../views/emp/' + component + '.vue'], resolve)
                }else if (component.startsWith("Per")){
                    require(['../views/per/' + component + '.vue'], resolve)
                }else if (component.startsWith("Sal")){
                    require(['../views/sal/' + component + '.vue'], resolve)
                }else if (component.startsWith("Sta")){
                    require(['../views/sta/' + component + '.vue'], resolve)
                }else if (component.startsWith("Sys")){
                    require(['../views/sys/' + component + '.vue'], resolve)
                }
            }
        };
        fmRoutes.push(fmRouter);
    });
    //console.log(fmRoutes);
    return fmRoutes;
};

