<template>
    <div>
        <el-form :model="register" :rules="rules" ref="registerForm" class="registerForm">
            <h2 style="text-align: center">注册</h2>
            <el-form-item prop="username">
                <el-input v-model="register.username" placeholder="请输入用户名" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item prop="password">
                <el-input v-model="register.password" placeholder="请输入密码" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item prop="passwordAgain">
                <el-input v-model="register.passwordAgain" placeholder="请确认密码" auto-complete="off"></el-input>
            </el-form-item>
            <el-button type="primary" style="width: 100%" @click="submitRegister">注册</el-button>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "Register",
        data() {
            return {
                register: {
                    username: '',
                    password: '',
                    passwordAgain: ''
                },
                rules: {
                    username: [{ required: true, message: '请输入用户名', trigger: 'blur'}],
                    password: [{ validator: validatePass, trigger: 'blur' }],
                    passwordAgain: [{ validator: validatePass2, trigger: 'blur'}]
                }
            };
            var validatePass = (rule, value, callback) => {
                alert(11);
                if (value === '') {
                    callback(new Error('请输入密码'));
                } else {
                    if (this.register.passwordAgain !== '') {
                        this.$refs.registerForm.validateField('passwordAgain');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                alert(value);
                if (value === '') {
                    callback(new Error('请再次输入密码'));
                } else if (value !== this.register.password) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            };
        },
        methods: {
            submitRegister() {
                this.$refs.registerForm.validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        this.$message.error("请填写完整信息");
                        return false;
                    }
                })
            }
        }

    }
</script>

<style>
    .registerForm {
        width: 350px;
        background-clip: padding-box;
        border: 1px solid rgba(229, 224, 235, 0.85);
        margin: 140px auto;
        background: #fff;
        border-radius: 15px;
        padding: 15px 15px 30px 30px;
        box-shadow: 0 0 25px rgba(242, 242, 242, 0.63);
    }
</style>