<template>
    <div>
        <el-container>
            <el-header class="containHeaderz">
                <div class="headTitle">石材世界</div>
                <div>
                    <el-dropdown v-if="this.user != null"   class="userAbout" @command="commandHeader">
                        <span   class="userInfoz">
                            {{user.name}}
                            <i><img :src="user.userface " alt=""></i>
                        </span>
                        <el-dropdown-menu slot="dropdown" >
                            <el-dropdown-item command="userInfo">个人中心</el-dropdown-item>
                            <el-dropdown-item command="setting">设置</el-dropdown-item>
                            <el-dropdown-item command="back" :hidden="isHidden">后台管理</el-dropdown-item>
                            <el-dropdown-item command="logout" divided>注销登陆</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                    <div v-else >
                    <el-button  type="text" @click="goLogin"
                                style="font-size: 20px;color: white;margin-right: 6px">登录
                    </el-button>
                    <el-button   type="text" @click="register"
                                 style="font-size: 20px;color: white;margin-right: 40px">注册</el-button>
                    </div>
                </div>
            </el-header>
            <el-main></el-main>
            <el-footer></el-footer>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "Front",
        data(){
            return{
                isHidden:true,
                user:JSON.parse(window.sessionStorage.getItem("user")),
            }
        },
        mounted(){
            this.intoBack();
        },
        methods:{
            intoBack(){
                if (this.user) {
                    if (this.user.username == 'admin' || this.user.username == 'libai') {
                        this.isHidden=false;
                    }
                }else {
                    this.isHidden=true;
                }
            },
            register(){
                this.$router.push("/register");
            },
            goLogin(){
                this.$router.push("/login");
            },
            commandHeader(arg){
                if (arg == 'logout') {
                    this.$confirm('此操作将退出登录, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.getRequest("/logout");
                        window.sessionStorage.removeItem("user");
                        this.$store.commit('initRoutes',[]);
                        location.reload();  //此处可能是个坑
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消操作'
                        });
                    });
                }
                if (arg == 'back'){
                    this.$router.replace("/back");
                }
            }
        }
    }
</script>

<style>
    .containHeaderz {
        background-color: black;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0px 15px;
        box-sizing: border-box;
        color: white;
    }
    .containHeaderz .headTitle {
        font-size: 30px;
        margin-left: 10px;
    }
    .userAbout{
        cursor: pointer;
    }
    .userInfoz{
        color: white;
        font-size: 14px;
        margin-right: 20px;
        display: flex;
        align-items: center;
    }
    .userInfoz img{
        width: 30px;
        height: 30px;
        border-radius: 20px;
        margin-left: 10px;
    }
    .loginz{
        margin-right: 20px;
        color: white;
        font-size: 20px;
    }

</style>