<template>
    <div>
        <el-container>
            <!-- 上面的导航栏 -->
            <el-header class="containHeader">
                <div class="title">石材后台管理</div>
                <div>
                    <el-dropdown class="userInfo" @command="commandHeader">
                        <span class="el-dropdown-link">
                            {{user.name}}<i><img :src="user.userface" alt=""></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="userInfo">个人中心</el-dropdown-item>
                            <el-dropdown-item command="setting">个人设置</el-dropdown-item>
                            <el-dropdown-item command="" divided>返回到登录</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </el-header>
            <!-- 左侧导航栏 -->
            <el-container>
                <el-aside width="220px">
                        <el-menu @select="menuSelect" unique-opened>
                            <el-submenu :index="index+''" v-for="(item,index) in routes"
                                v-if="!item.hidden" :key="index">
                                <template slot="title">
                                    <i style="color: blue;margin-right: 8px;" :class="item.iconcls"></i>
                                    <span>{{item.name}}</span>
                                </template>
                                    <el-menu-item :index="child.path" v-for="(child,indexj) in item.children"
                                                  :key="indexj">
                                        {{child.name}}</el-menu-item>
                            </el-submenu>
                        </el-menu>
                </el-aside>
                <el-main>
                    <el-breadcrumb separator-class="el-icon-arrow-right"
                    v-if="this.$router.currentRoute.path !='/back'">
                        <el-breadcrumb-item :to="{ path: '/back' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item>{{this.$router.currentRoute.name}}</el-breadcrumb-item>
                    </el-breadcrumb>              <!-- 这里的name是后台传过来的一组数据 -->
                    <div class="homeWelcome" v-if="this.$router.currentRoute.path == '/back'">
                        欢迎来到石材后台管理
                    </div>
                    <router-view class="homeRouterView"/>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "Back",
        data() {
            return {
                user: JSON.parse(window.sessionStorage.getItem("user")),
            }
        },
        computed:{
            routes(){
                return this.$store.state.routes;
            }
        },
        methods:{
            menuSelect(index){
                  this.$router.push(index);
            },
            commandHeader(command){
                if (command == ''){
                    this.$confirm('此操作将返回至于登录页面, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.getRequest("/logout");
                        window.sessionStorage.removeItem("user");
                        this.$store.commit('initRoutes',[]);
                        this.$router.replace("/");
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消操作'
                        });
                    });
                }
                if (command == 'front'){
                    this.$confirm('此操作将返回到前台页面, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.$router.replace("/");
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消操作'
                        });
                    });
                }
            }
        }
    }
</script>

<style>
    .homeRouterView{
        margin-top: 10px;
    }
    .homeWelcome{
        font-size: 30px;
        text-align: center;  /**居中显示**/
        padding-top: 100px;
        font-family: 楷体;
        color: blue;
    }
    .containHeader {
        background-color: blue;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0px 15px;
        box-sizing: border-box;
        color: #f5f7fa;
    }

    .containHeader .title {
        font-size: 30px;
        font-family: 楷体;
        margin-left: 10px;
    }
    .userInfo {
        font-size: 26px;
        color: rgba(242,242,242,0);
        cursor: pointer;
    }
    .el-dropdown-link{
        color: aliceblue;
        margin-right: 20px;
        display: flex;
        align-items: center;
    }
    .el-dropdown-link img{
        width: 30px;
        height: 30px;
        border-radius: 20px;
        margin-left: 10px;
    }
</style>