<template>
    <div>
        <el-form :model="LoginForm" ref="loginForm" :rules="rules"  class="loginContainer">
            <h2 style="text-align: center">系统登陆</h2>
            <el-form-item prop="username">
                <el-input v-model="LoginForm.username" auto-complete="off" placeholder="请输入用户名"></el-input>
            </el-form-item>
            <el-form-item prop="password" >
                <el-input v-model="LoginForm.password" auto-complete="off" placeholder="请输入密码"
                @keydown.enter.native="submitLogin"></el-input>
            </el-form-item>
            <!--<el-checkbox v-model="checked"></el-checkbox>-->
            <el-button type="primary" style="width:100%" @click="submitLogin">登陆</el-button>
        </el-form>
    </div>
</template>

<script>

    export default {
        name: "Login",
        data(){
            return{
                LoginForm:{
                    username:'admin',
                    password:'123'
                },
                checked:false,
                rules:{
                    username:[{ required: true, message: '请输入用户名', trigger: 'blur' }],
                    password:[{ required: true, message: '请输入密码', trigger: 'blur' }]
                }
            }
        },
        //关于表单的处理，1是要验证，2是要数据
        methods:{
            submitLogin(){
                this.$refs.loginForm.validate((valid) => {
                    if (valid) {
                        this.postKeyValueRequest("/doLogin",this.LoginForm).then(resp=>{
                            //这个resp是代表response.data 也就是服务端返回的json
                            if (resp) {   //如果进入到这里说明已经成功返回
                                //用户信息保存到sessionStorage中
                                //这个resp.obj就是服务端返回的用户信息
                                window.sessionStorage.setItem("user",JSON.stringify(resp.obj));
                                let path = this.$route.query.redirect;
                                this.$router.replace((path=='/'||path==undefined)?"/back":path);
                            }
                        })
                        //alert('submit!');
                    } else {
                        this.$message.error("请填写完整信息");
                        return false;
                    }
                });
            }
        }
    }
</script>

<style>
    .loginContainer{
        border-radius: 15px;
        background-clip: padding-box;
        margin: 180px auto;
        width: 350px;
        padding: 15px 35px 15px 35px;
        background: #fff;
        border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #cac6c6;
    }
</style>