import axios from 'axios';
import { Message } from 'element-ui';
import router from '../router/index'

axios.interceptors.response.use(success=>{
    if (success.status && success.data.status == 200 && success.data.status == 500){
        //业务错误
        Message.error({message:success.data.msg});
        return; //什么都不会返回给你
    }
    if (success.data.msg){   //成功的话也会展示成功的信息
        Message.success({message:success.data.msg})
    }
    return success.data; //把服务端的的数据返回给你。
},error => {
    if (error.response.status == 504 || error.response.status == 404){
        return Message.error({message:"没有找到服务器"});
    }  else if (error.response.status == 403){
        return Message.error({message:"权限不足"});
    }  else if (error.response.status == 401){
        router.replace('/');
        return Message.error({message:"尚未登录"});
    } else {
        if (error.response.data.msg){
            alert(223)
            return Message.error({message:error.response.data.msg})
        } else {
            return Message.error({message:"未知错误"})
        }
    }
});


export const postKeyValueRequest = (url,params)=>{
    return axios({
        method:'post',
        url:`${url}`,
        data:params,
        transformRequest:[function (data) {
            //console.log(data);
            let ret = '';
            for (let i in data){
                ret += encodeURIComponent(i)+"="+encodeURIComponent(data[i])+"&"
            }
            //console.log(ret);
            return ret;
        }],
        header:{  //请求头
            'Content-Type':'application/x-www-form-urlencoded'
        }
    })
};

//这个就类似发ajax请求，其中请求的参数、方法、数据等都是传递进去
export const postRequest = (url,params)=>{
    return axios({
        method:'post',
        url:`${url}`,
        data:params
    })
};
//axios.get('http://localhost:8081/doLogin?username=admin&password=123').then(function(resp){})的封装
export const getRequest = (url,params)=>{  //url=doLogin  params = username
    return axios({
        method:'get',
        url:`${url}`,
        data:params
    })
};

export const putRequest = (url,params)=>{  // => 就代表function的返回
    return axios({
        method:'put',
        url:`${url}`,
        data:params
    })
};

export const deleteRequest = (url,params)=>{
    return axios({
        method:'delete',
        url:`${url}`,
        data:params
    })
};
