import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import Register from '../views/Register.vue'
import Front from '../views/Front.vue'
import Back from '../views/Back.vue'

Vue.use(VueRouter);

const routes = [
    {
        path: '/front',
        name: 'Front',
        component: Front,
        hidden:true
    },
    {
        path: '/',
        name: 'Login',
        component: Login,
        hidden:true
    },
    {
        path: '/register',
        name: 'Register',
        component: Register,
        hidden:true
    },
    {
        path: '/back',
        name: 'Back',
        component: Back,
        hidden:true
        // meta:{
        //     role:[
        //         'admin','user'
        //     ]
        // }
    }
];

const router = new VueRouter({
    routes
});

export default router
